.header {
  z-index: 1000;
  width: 100%;
  height: 100vh;
  background: #2f303a;
  justify-content: center;
  align-items: center;
  transition: all 1s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.header.sticky {
  height: 80px;
}

.banner {
  width: 100%;
  height: 100%;
  background: #182431;
  transition: all 1s;
  position: absolute;
  top: 0;
  left: 0;
}

.header.sticky .banner {
  opacity: .5;
}

.logo {
  color: #fff;
  z-index: 1;
  font-size: 15vw;
  font-weight: 700;
  transition: all 1s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header.sticky .logo {
  font-size: 2em;
  position: absolute;
  left: 100px;
}

.header.sticky .logo .logo-photo {
  width: 80px;
}

.nav {
  max-width: 80%;
  z-index: 1;
  justify-content: flex-end;
  margin-left: auto;
  display: flex;
  position: relative;
}

.nav-list {
  opacity: 0;
  transition: all .25s;
  display: flex;
  position: relative;
  transform: translateX(-100px);
}

.header.sticky .nav-list {
  opacity: 1;
  visibility: visible;
  transition-delay: .75s;
  transform: translateX(0);
}

.nav-list__link {
  letter-spacing: .02em;
